.banner {
  height: 10rem;
  text-align: center;
  width: 100%;
  background: theme-color('secondary');
  padding-top: 1rem;
}

.banner .nav li {
  @extend .nav-item;
}

.banner .nav a {
  @extend .nav-link;
}

header.banner.sm-hidden {
  background: theme-color('secondary');
}

/////////
// MENU 
// https://codemyui.com/slide-nav-menu-off-trigger-area/
$timing-function : cubic-bezier(0.190, 1.000, 0.220, 1.000);
$nav-width-desktop : 100%;
$nav-width-mobile : 25rem;
$nav-item-nb : 4;

#menu-menu-principal-1 {
  justify-content: center;
}

.wrapper-main-menu {
  input {
    position: fixed;
    opacity: 0;
  }

  label {
    position: absolute;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;

    &::before {
      position: fixed;
      z-index: 1;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: theme-color(secondary);
      content: '';
      opacity: 0;
      pointer-events: none;
      transition: opacity .5s $timing-function;
    }

    .burger {
      position: fixed;
      top: 0;
      left: 0;
      z-index: 3;
      width: 3em;
      height: 3em;
      margin: 0;
      padding: 0.5rem;
      background-color: darken(theme-color(primary), 15%);
      border: 15px solid darken(theme-color(primary), 15%);
      transition: opacity .5s $timing-function;

      &::before,
      .bar,
      &::after {
        position: absolute;
        left: 0;
        display: block;
        width: 100%;
        height: 12%;
        background: white;
        content: '';
        transition: all .5s $timing-function;
      }

      .bar {
        top: 44%;
      }

      &::before {
        top: 0;
        transform-origin: top left;
      }

      &::after {
        bottom: 0;
        transform-origin: bottom left;
      }
    }
  }

  input:focus+label,
  label:hover {
    .burger {
      opacity: 1;
    }
  }

  nav {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: flex-start;
    transform: translate3d(0, 0, 0);
    /* material acceleration */
    transform: translateX(-100%);
    will-change: transform;
    transition: transform .5s $timing-function;
    // background-color: darken(theme-color(primary), 25%);
    overflow: auto;
    padding: 1rem;

    &::before {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      width: $nav-width-mobile;
      background: theme-color(primary);
      content: '';
      transform: skewX(-165deg) translateX(-100%);
      transform-origin: bottom left;
      will-change: transform;
      transition: transform .5s $timing-function;

      @media(min-width: 40em) {
        width: $nav-width-desktop;
      }
    }

    p {
      max-width: 70%;
    }

    a {
      color: white;
      text-decoration: none;
      font-weight: 500;
      transform: translateX(-100%);
      transition: color .15s, transform .5s;
      transition-timing-function: $timing-function;

      @for $i from 1 through $nav-item-nb {
        &:nth-child(#{$i}) {
          transition-delay: 0s,
          #{50 + (50 * $i)}ms;
        }
      }

      &:hover,
      &:focus {
        color: black;
      }
    }
  }

  #menu-main-menu {
    ul>li {}

    // RUBRIQUES NIVEAU 0
    .menu-item {}

    // RUBRIQUES NIVEAU 1
    .sub-menu {
      .menu-item {}
    }
  }

}



@include media-breakpoint-up(md) {


  #menu-md {}
}

main {
  overflow: hidden;

  .content {
    transform: translate3d(0, 0, 0);
    /* material acceleration */
    will-change: transform, filter;
    transition: all .5s $timing-function;
  }
}

[id="navcheck"]:checked {
  &+label {
    &::before {
      opacity: 1;
      pointer-events: auto;
    }

    .burger {

      &::before,
      &::after {
        width: 141.42%; // Pythagore!
      }

      &::before {
        transform: rotate(45deg) translateY(-50%);
      }

      &::after {
        transform: rotate(-45deg) translateY(50%);
      }

      .bar {
        transform: scale(0.1);
      }
    }
  }

  &~nav {
    transform: translateX(0);

    &::before {
      transform: skewX(-5deg) translateX(0);
      margin-left: -7rem;
    }

    a {
      transform: translateX(0);
    }
  }

  &~main .content {
    transform: translateX(3em);
    transform-origin: left center;
    filter: blur(2px);
  }
}



